@import 'colors';
@import 'fonts';

@import 'sections/principles';

html {
	font-size: 62.5%; }
body {
	@include f-regular;
	color: $black; }
p, h1 {
	margin: 0; }

h1 {
	@include f-bold; }

.border {
	border: 1px solid black;
	&-r {
		border: 1px solid red; } }

.glob-wrap {}

.section {
	&__title {
		font-size: 5.5rem;
		line-height: 8rem;
		margin-bottom: 6rem;
		display: flex;
		align-items: flex-end;
		position: relative; } }

.container {
	display: flex;
	flex-direction: column;
	align-items: center; }



